*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}

.header {
  padding: 2rem 0;
  text-align: center;
}

.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}

textarea {
  display: block;
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  height: 200px;
  padding: 1rem;
  margin-top: 1rem;
  margin-bottom: 5rem;
  border: 5px solid #d9d9d9;
  border-radius: 10px;
}

.keyboard-wrapper {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  padding: 3rem 40px;
  border-radius: 20px;
  background-color: #d9d9d9;
  background-image: linear-gradient(315deg, #d9d9d9 0%, #f6f2f2 74%);
  -webkit-box-shadow: 0px 0px 5px 10px rgba(17, 68, 107, 0.2);
  -moz-box-shadow: 0px 0px 5px 10px rgba(17, 68, 107, 0.2);
  box-shadow: 0px 0px 5px 10px rgba(17, 68, 107, 0.2);
}

.keyboard__row {
  display: flex;
  gap: 10px;
}

.key-button {
  position: relative;
  width: 60px;
  border: none;
  height: 60px;
  border-radius: 10px;
  color: #fff;
  background: linear-gradient(180deg, #282828, #292929);
  box-shadow: inset -8px 0 8px rgba(0, 0, 0, 0.15), inset 0px -8px 8px rgba(0, 0, 0, 0.25), 0 0 0 2px rgba(0, 0, 0, 0.75), 10px 20px 25px rgba(0, 0, 0, 0.4);
  /* border: 2px solid #5a5a5a; */
  overflow: hidden;
}
.key-button:active {
  transform: scale(0.9);
}
.key-button:active:before {
  background: linear-gradient(90deg, #d8d8d8, #202020);
}

.key-button:before {
  content: "";
  position: absolute;
  top: 3px;
  left: 4px;
  bottom: 14px;
  right: 12px;
  border-radius: 10px;
  background: linear-gradient(90deg, #282828, #202020);
  box-shadow: -10px -10px 10px rgba(255, 255, 255, 0.25), 10px 5px 10px rgba(0, 0, 0, 0.15);
  border-left: 1px solid rgba(0, 0, 0, 0.2666666667);
  border-bottom: 1px solid rgba(0, 0, 0, 0.2666666667);
  border-top: 1px solid rgba(0, 0, 0, 0.2666666667);
}

.key-button .key-value {
  position: relative;
  z-index: 1;
}
.key-button .key-shift {
  position: relative;
  z-index: 1;
}

.key-button.active {
  transform: scale(0.9);
}
.key-button.active:before {
  background: linear-gradient(90deg, #d8d8d8, #202020);
}

button[value=Tab] {
  width: 105px;
}

button[value=CapsLock] {
  width: 145px;
}

button[value=ShiftRight] {
  width: 135px;
}

button[value=ShiftLeft] {
  width: 170px;
}

button[value=Space] {
  width: 450px;
}

button[value=Backspace] {
  width: 175px;
}

button[value=Enter] {
  width: 160px;
}